import { Component, OnInit } from '@angular/core';
import { API_URL } from '../../constants';
import { Http } from '@angular/http';
import { Book } from '../../models/book.model';
import { BookService } from '../book.service';
import { ComponentBase } from '../component.base';

@Component({
  selector: 'app-book-list',
  templateUrl: './book-list.component.html',
  styleUrls: ['./book-list.component.scss']
})
export class BookListComponent extends ComponentBase implements OnInit {

  public books: Book[];

  constructor(private bookService: BookService) {
    super();

    this.disposeOnDestroy(bookService.book$.subscribe(books => this.books = books));
  }

  ngOnInit() {
    this.bookService.getAll();
  }

  public deleteBook(id: number): void {
    this.disposeOnDestroy(this.bookService.deleteBook(id)
      .subscribe(
        _ => alert('book deleted'),
        err => {
          alert('could not delete book, check log for details');
          console.log(err);
        }
      )
    );
  }

}



