import { Component, OnInit } from '@angular/core';
import { Book } from '../../models/book.model';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BookService } from '../book.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ComponentBase } from '../component.base';

@Component({
  selector: 'app-edit-book',
  templateUrl: './edit-book.component.html',
  styleUrls: ['./edit-book.component.scss']
})
export class EditBookComponent extends ComponentBase implements OnInit {

  public bookForm: FormGroup;
  public book: Book;

  constructor(private fb: FormBuilder, private bookService: BookService, private router: Router, route: ActivatedRoute) {
    super();

    const book$ = route.params
      .filter(params => params['id'])
      .map(params => params['id'])
      .switchMap(id => this.bookService.book$
        .map(books => books.find(b => b.id == id))
      );

    this.disposeOnDestroy(book$.subscribe(book => {
      this.book = book;
      if(this.bookForm) {
        this.patchFormValue(book);
      }
    }));
  }


  ngOnInit() {
    this.bookForm = this.fb.group({
      author: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      title: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      subject: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      publisher: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      isbn: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      lang: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      inStock: [true, Validators.required]
    });

    if(this.book) {
      this.patchFormValue(this.book);
    }
  }

  public updateBook(): void {
    if(!this.bookForm.valid) return;

    const book: Book = {
      id: this.book.id,
      author: this.bookForm.controls['author'].value,
      title: this.bookForm.controls['title'].value,
      subject: this.bookForm.controls['subject'].value,
      publisher: this.bookForm.controls['publisher'].value,
      isbn: this.bookForm.controls['isbn'].value,
      language: this.bookForm.controls['lang'].value,
      inStock: this.bookForm.controls['inStock'].value
    };
    this.disposeOnDestroy(this.bookService.updateBook(book)
      .subscribe(res => {
          alert('Book updated successfully!');
          const resultBook = res.json();
          this.router.navigate(['/books', resultBook.id]);
        },
        err => {
          alert('could not update book. check log for details');
          console.log(err);
        }
      )
    );
  }

  private patchFormValue(book: Book): void {
    this.bookForm.setValue({
      author: book.author,
      title: book.title,
      subject: book.subject,
      publisher: book.publisher,
      isbn: book.isbn,
      lang: book.language,
      inStock: book.inStock
    })
  }
}



