import { Component, OnInit } from '@angular/core';
import { Book } from '../../models/book.model';
import { BookService } from '../book.service';
import { ComponentBase } from '../component.base';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-book-details',
  templateUrl: './book-details.component.html',
  styleUrls: ['./book-details.component.scss']
})
export class BookDetailsComponent extends ComponentBase implements OnInit {

  public book: Book;

  constructor(private bookService: BookService, private router: Router, route: ActivatedRoute) {
    super();
    let id$ = route.params
      .filter(params => params['id'])
      .map(params => params['id']);

    this.disposeOnDestroy(id$.subscribe(id => this.bookService.get(id)));
    this.disposeOnDestroy(id$.switchMap(id => this.bookService.book$
      .map(books => books.find(b => b.id == id)))
      .subscribe(book => {
        console.log(book);
        this.book = book;
      })
    )
  }

  ngOnInit() {

  }

  public deleteBook() {
    this.disposeOnDestroy(this.bookService.deleteBook(this.book.id)
      .subscribe((res) => {
          alert('Book deleted successfully');
          this.router.navigate(['/']);
        },
        err => {
          alert('could not delete book, check log for details');
          console.log(err);
        }
      )
    );
  }

}



