import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Book } from '../../models/book.model';
import { ComponentBase } from '../component.base';
import { BookService } from 'app/book.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-new-book',
  templateUrl: './new-book.component.html',
  styleUrls: ['./new-book.component.scss']
})
export class NewBookComponent extends ComponentBase implements OnInit {

  public newBookForm: FormGroup;

  constructor(private fb: FormBuilder, private bookService: BookService, private router: Router) {
    super();
  }

  ngOnInit() {
    this.newBookForm = this.fb.group({
      author: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      title: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      subject: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      publisher: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      isbn: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      lang: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      inStock: [true, Validators.required]
    });
  }

  public createBook(): void {
    if(!this.newBookForm.valid) return;

    const book: Book = {
      author: this.newBookForm.controls['author'].value,
      title: this.newBookForm.controls['title'].value,
      subject: this.newBookForm.controls['subject'].value,
      publisher: this.newBookForm.controls['publisher'].value,
      isbn: this.newBookForm.controls['isbn'].value,
      language: this.newBookForm.controls['lang'].value,
      inStock: this.newBookForm.controls['inStock'].value
    };
    this.disposeOnDestroy(this.bookService.createBook(book)
      .subscribe(res => {
        alert('Book created successfully!');
        this.router.navigate(['/']);
      },
        err => {
          alert('could not create book. check log for details');
          console.log(err);
        }
      )
    );
  }


}



