import { Injectable } from '@angular/core';
import { Http, RequestOptions, Response, Headers } from '@angular/http';
import { BehaviorSubject, Observable } from 'rxjs/Rx';
import { Book } from '../models/book.model';
import { API_URL } from '../constants';

@Injectable()
export class BookService {

  private bookSubject: BehaviorSubject<Book[]>;

  get book$() {
    return this.bookSubject.asObservable();
  }

  constructor(private http: Http) {
    this.bookSubject = new BehaviorSubject([])
  }

  public getAll(): Observable<Book[]> {
    this.http.get(`${API_URL}/books`)
      .map(res => res.json())
      .subscribe((books: Book[]) => {
        this.bookSubject.next(this.sortBooks(books));
      });
    return this.book$;
  }

  public search(searchTerm: string, includeNotIntStock: boolean): Observable<Book[]> {
    this.http.get(`${API_URL}/books?search=${searchTerm}&includeNotInStock=${includeNotIntStock}`)
      .map(res => res.json())
      .subscribe(books => this.bookSubject.next(books));

    return this.book$;
  }

  public get(id: number): void {
    this.http.get(`${API_URL}/books/${id}`)
      .map(res => res.json())
      .subscribe(book => {
        const remaing = this.bookSubject.value.filter(b => b.id != id);
        const result = this.sortBooks([...remaing, book]);
        this.bookSubject.next(result);
      });
  }

  public deleteBook(id: number) {
    return this.http.delete(`${API_URL}/books/${id}`)
      .do(_ => {
        const remaining = this.bookSubject.getValue().filter(b => b.id != id);
        this.bookSubject.next(this.sortBooks(remaining));
      })
  }

  public createBook(newBook: Book): Observable<Response> {
    const json = JSON.stringify(newBook);

    return this.http.post(`${API_URL}/books`, json, this.createRequestOptions())
      .do(res => {
        const book = res.json();
        const existing = this.bookSubject.getValue();
        this.bookSubject.next([...existing, book]);
      })
  }

  public updateBook(newBook: Book): Observable<Response> {
    const json = JSON.stringify(newBook);

    return this.http.put(`${API_URL}/books/${newBook.id}`, json, this.createRequestOptions())
      .do(res => {
        const book = res.json();
        const existing = this.bookSubject.getValue().filter(b => b.id != book.id);
        this.bookSubject.next(this.sortBooks([...existing, book]));
      })
  }

  private sortBooks(books: Book[]): Book[] {
    return books.sort((a, b) => a.id - b.id);
  }

  private createRequestOptions(): RequestOptions {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });

    return options;
  }
}



